@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&family=Open+Sans&display=swap');


.header {
    position: relative;
    height: 50vh;
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
    background-color: #333;
    background-image: url('https://cdn.pixabay.com/photo/2019/09/19/15/46/apartment-4489573_1280.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Montserrat', sans-serif;

    &_shadow {
        position: absolute;
        background-color: rgba(0, 0, 0, 0.701);
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 100;
    }

    &_text {
        position: absolute;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-left: 90px;
        height: 100%;
        width: 100%;
        color: #FFF;
        font-size: 20px;
        z-index: 1000;
    }
}

@media screen and (max-width: 900px) {
    .header {
        &_text {
            justify-content: center;
            margin-left: 0;
            text-align: center;
            font-size: 15px;
            margin: 0 10px;
        }
    }
}